.main {
    padding: 10px;
    /* height: 170vh; */
    background-color: #abd5ff;
    background-image: url("../../public/binding-dark-1920x1080.png");
    background-size: cover;
}

.layout {
    display: flex;
}

.sidebar {
    height: 100vh;
    width: 300px;
    border-radius: 5px;
    background-color: #94caff;
    box-shadow: 0 0 2px rgb(192, 192, 238);
    margin-right: 20px;
    color: whitesmoke;
}

.content {
    width: 100%;
    height: auto;
}

.header {
    height: 10vh;
    margin-bottom: 20px;
    box-shadow: 0 0 2px rgb(192, 192, 238);
    background-color: #94caff;
    border-radius: 10px;
}

.body {
    min-height: 85vh;
    margin-bottom: 20px;
}

.logo h6 {
    font-size: 1.5rem;
    text-align: center;
    margin: 20px 0;
}

.menu {
    margin-top: 50px;
}

.menu-item {
    margin-top: 30px;
}

.menu-item a {
    color: white;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i {
    font-size: 1.2rem;
    margin: 0 15px;
}

.active {
    background-color: white;
    padding: 10px;
}

.active a,
.active i {
    color: rgb(133, 192, 245);
}

.header-content {
    display: flex;
    align-items: center;
    height: 50px;
    justify-content: flex-end;
    margin: 0px 15px;
    padding-top: 15px;
}

.header-content i {
    margin-right: 10px;
    font-size: 1.2rem;
}

.header-content a {
    text-decoration: none;
    color: blue;
    margin: 0 10px 0 15px;
    text-transform: uppercase;
}